<template>
  <div id="app">
    <div class="container-fluid">
      <Header/>
      <hr style="border: none">
      <div class="row mt-5 student-reg">
        <div class="text-center" >
          <div class="main-title">忘记密码</div>
          <div class="row mt-6" style="margin-top: 50px">
            <div class="center-block" style="width:300px;">
              <div class="row text-center">
                <el-form  class="center-block mt-3" ref="form" :model="form" :rules="rules" :show-message="true">
                  <el-form-item prop="phone">
                    <el-input placeholder="你的手机号" autocomplete="off" v-model="form.phone" maxlength="11"></el-input>
                  </el-form-item>
                  <el-form-item  prop="verify">
                    <el-input placeholder="验证码" autocomplete="off" v-model="form.verify" maxlength="6"></el-input>
                    <el-popover
                      ref="verifyCodePopover"
                      placement="right"
                      trigger="click">
                      <slide-verify :l="42"
                                    :r="10"
                                    :w="310"
                                    :h="155"
                                    @success="onSuccess"
                                    @fail="onFail"
                                    @refresh="onRefresh"
                                    @changeNum = "change"
                                    :sliderText="'请向右滑动'"
                                    :setNum = "60"
                                    ref="slideVerify"
                      ></slide-verify>
                      <el-button slot="reference" v-if="!canGetVerify" disabled class="verifycode el-input__inner">{{verifyCodeText}}</el-button>
                      <el-button slot="reference" v-if="canGetVerify"  :disabled="countdownshow" class="verifycode el-input__inner"><span v-show="!countdownshow">{{verifyCodeText}}</span><span v-show="countdownshow">{{countdown}}s</span></el-button>
                    </el-popover>
                  </el-form-item>
                  <el-form-item  prop="pwd">
                    <el-input placeholder="设置密码" autocomplete="off" v-model="form.pwd" show-password maxlength="20"></el-input>
                  </el-form-item>
                  <div class="row" style="width: 330px;margin: unset" v-show="this.form.pwd != ''">
                    <div class="pull-right" style="display: flex">
                      <div :class="level != 0?'level-color':''" style="width:41px;height:5px;background:#E8E8E8;border-radius:3px;margin-right: 5px;margin-bottom: 16px"></div>
                      <div :class="level ==2 || level == 3?'level-color':''" style="width:41px;height:5px;background:#E8E8E8;border-radius:3px;margin-right: 5px;margin-bottom: 16px"></div>
                      <div :class="level == 3?'level-color':''" style="width:41px;height:5px;background:#E8E8E8;border-radius:3px;margin-bottom: 16px; margin-right: 10px;"></div>
                    </div>
                  </div>
                  <el-form-item prop="pwd">
                    <el-input placeholder="确认密码" v-model="form.pwd2" show-password maxlength="20"></el-input>
                  </el-form-item>
                </el-form>
              </div>
              <div class="row mt-6" style="margin-top: 50px">
                <div class="btn btn-block reg-btn" @click="resetPwd('form')">重置密码</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer/>
    </div>
  </div>
</template>

<script>
import Header from '../../components/reg/Header.vue'
import Footer from '../../components/reg/Footer.vue'
export default {
  name: 'app',
  components: {
    Header,
    Footer
  },
  data () {
    var validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入手机号'))
      } else {
        if (this.form.phone !== '') {
          var reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/
          if (!reg.test(this.form.phone)) {
            callback(new Error('手机号格式不正确'))
          }
        }
        callback()
      }
    }
    var filterPhone = (rule, value, callback) => {
      this.form.phone = value.replace(/[^\d]/g, '')
      callback()
    }
    var filterVerify = (rule, value, callback) => {
      this.form.verify = value.replace(/[^\d]/g, '')
      callback()
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.form.pwd2 !== '') {
          if (this.form.pwd2 !== this.form.pwd) {
            callback(new Error('两次密码输入必须一致！'))
          }
        }
        callback()
      }
    }
    var filterPass = (rule, value, callback) => {
      var patrn = /^(\w){6,20}$/
      if (!patrn.exec(value)) {
        callback(new Error('只能输入6-20个字母、数字、下划线 '))
      }
    }
    return {
      msg: '',
      level: '', // 密码强度等级
      canGetVerify: '', // 此时能否获取验证码
      verifyCodeText: '获取验证码', // 验证码按钮显示内容
      interval: null, // 验证码码定时器
      countdown: '', // 验证码倒计时秒数
      countdownshow: false, // 验证码倒计时秒数显示标志
      form: {
        phone: '',
        verify: '',
        pwd: '',
        pwd2: ''
      },
      rules: {
        phone: [
          { min: 11, max: 11, message: '手机号长度应为11位', trigger: 'blur' },
          { validator: validatePhone, trigger: 'blur' },
          { validator: filterPhone, trigger: 'change' }
        ],
        verify: [
          { validator: filterVerify, trigger: 'change' },
          { required: true, message: '请输入短信验证码', trigger: 'blur' }
        ],
        pwd: [
          { validator: validatePass, trigger: 'blur' },
          { validator: filterPass, trigger: 'change' }
        ],
        pwd2: [
          { required: true, message: '请输入确认密码', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
  },
  watch: {
    'form.pwd': {
      deep: true,
      handler: function () {
        var pwd = this.form.pwd
        var reg1 = /\d/
        var reg2 = /[a-zA-Z]/
        var reg3 = /\W/
        var level = 0
        if (reg1.test(pwd)) {
          // 密码等级增加
          level++
        }
        if (reg2.test(pwd)) {
          // 密码等级增加
          level++
        }
        if (reg3.test(pwd)) {
          // 密码等级增加
          level++
        }
        this.level = level
      }
    },
    'form.phone': {
      deep: true,
      handler: function () {
        var reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/
        if (this.form.phone === null || this.form.phone === undefined || !reg.test(this.form.phone)) {
          this.canGetVerify = false
        } else {
          this.canGetVerify = true
        }
      }
    }
  },
  methods: {
    onSuccess () {
      let self = this
      self.$refs['verifyCodePopover'].doClose()
      self.$refs.slideVerify.reset()
      function callback () {
        self.$message('验证成功,请注意查收短信。')
        self.countdown = 60
        self.countdownshow = true
        // 开始计时
        self.interval = setInterval(() => {
          self.countdown--
          if (self.countdown <= 0) {
            self.verifyCodeText = '重新发送'
            self.countdownshow = false
            clearInterval(self.interval)
          }
        }, 1000)
      }
      this.$http.post('/user/vcfraudprevent', {}, res => {
        if (res.success) {
          self.$http.post('/user/sendsmsverificationcode', {
            'phone': self.form.phone,
            'msgType': 'register',
            'sign': res.data.sign
          }, res => {
            if (res.success) {
              callback()
            }
          })
        }
      })
    },
    onFail () {
      this.msg = ''
    },
    onRefresh () {
      console.log('ok----')
    },
    change (num) {
      console.log('num', num)
    },
    resetPwd (formName) {
      let me = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          me.$http.post('/user/resetpassword', {
            'userCellphone': me.form.phone,
            'userPassword': me.form.pwd,
            'smsVerificationCode': me.form.verify
          }, res => {
            if (res.success) {
              window.location = 'login.html'
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    checkPhone () {
      if (this.form.phone === null || this.form.phone === undefined) {
        this.$alert('提示', '手机号不能为空！')
        return false
      }
      var reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/
      if (!reg.test(this.form.phone)) {
        this.$alert('提示', '手机号格式错误！')
        return false
      }
    }
  }
}
</script>

